/* variables */
/* inject:mixins:less */
/* endinject */
/* base */
/*---------- TYPHOGRAFY ----------*/
/* Root elements (use pixels) */
html {
  font-size: 17px !default;
}
/* Standard document elements (use rem) */
.header {
  font-size: 1.5rem;
}
.footer {
  font-size: 0.75rem;
}
.sidebar {
  font-size: 0.85rem;
}
/* Standard module elements (use em) */
h1 {
  font-size: 3em !default;
}
h2 {
  font-size: 2.5em !default;
}
h3 {
  font-size: 2em !default;
}
h4 {
  font-size: 1.5em !default;
}
h5 {
  font-size: 1em !default;
}
.container,
.container-fluid {
  position: relative;
  font-size: 1rem;
}
.container {
  width: 90%;
  max-width: 1200px;
}
.container-fluid {
  width: 100%;
}
@media (max-width: 599px) {
  .container {
    font-size: 0.9rem;
    width: 100%;
  }
}
@media (max-width: 400px) {
  .container {
    font-size: 0.8rem;
  }
}
.container-padding {
  padding-left: 10px;
  padding-right: 10px;
}
.section-padding {
  padding: 2em 10px;
}
.section-padding-2x {
  padding: 4em 10px;
}
/* inject:components:less */
i.button-icon {
  width: 1em;
  text-align: center;
}
i.button-icon.icon-left {
  margin-right: 0.4em;
}
i.button-icon.icon-right {
  margin-left: 0.4em;
}
i.button-icon.icon-both {
  margin: 0 0.4em;
}
.nav-button,
.nav-button-flex {
  margin: 0;
  padding: 0 1em;
}
.nav-button-flex {
  display: flex;
}
.text-align-left {
  text-align: left;
}
.text-align-right {
  text-align: right;
}
.navbar-nav {
  height: 100%;
}
.navbar-nav > .md-button {
  color: #a03033!important;
}
.navbar-brand {
  margin-right: 1em;
  height: 100%;
}
.navbar-brand > img {
  height: 100%;
}
.truncate {
  display: flex;
  flex-direction: row;
}
.truncate > span {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
/* endinject */
/* inject:partials:less */
#page-companies .list-item-0 {
  background: url('/assets/images/backgrounds/eloptic.jpg') center no-repeat;
  background-size: cover;
}
#page-companies .list-item-0:before {
  background-color: rgba(255, 255, 255, 0.8);
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
}
.board-links {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.board-cards-container {
  display: flex;
  justify-content: center;
}
.board-cards-container .board-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1500px;
}
.board-cards-container .board-cards .board-card {
  display: flex;
  align-items: center;
  background-image: url("../../../assets/images/ETlogos/logo.svg");
  background-position: left;
  background-repeat: no-repeat;
  background-size: 7em;
  width: 300px;
  margin: 0px;
  padding: 0px;
}
.board-cards-container .board-cards .board-card .board-card-image {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  background-size: cover;
  margin: 10px 20px;
  border: 1px solid #a03033;
}
.board-cards-container .board-cards .board-card a {
  text-decoration: none;
  color: #a03033;
}
.board-cards-container .board-cards .board-card name {
  font-weight: bold;
}
#board-background {
  background-color: #ef5350;
}
#googlemaps {
  width: 500px;
  height: 400px;
}
.circular {
  border-radius: 50%;
  overflow: hidden;
}
.circular-200 {
  width: 200px;
  height: 200px;
}
.facebook-link {
  color: #133783;
  font-weight: 900;
  text-decoration: none;
}
.mailto-link {
  text-decoration: none;
  color: inherit;
}
.forgot-container {
  max-width: 30em;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}
.video-wrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  height: 0;
}
.video-wrapper > iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.programme-header {
  background-color: #fff;
  font-size: 1rem;
}
.programme-header > .header-content {
  margin-top: 20px;
}
.programme-header > .header-content img {
  flex: 0 0 auto;
  margin: 0 2em;
  width: 5em;
}
.programme-header > .header-content .md-display-2 {
  font-size: 3em;
  line-height: 2em;
}
.programme-header > .header-content .md-title {
  font-size: 2.2em;
  margin-left: 2.5em;
}
.skew-lb,
.skew-rb {
  position: relative;
}
.skew-lb > *,
.skew-rb > * {
  z-index: 1;
}
.skew-lb:before,
.skew-rb:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: inherit;
  top: 50%;
}
.skew-lb:before {
  transform: skewY(-3deg);
}
.skew-rb:before {
  transform: skewY(3deg);
}
@media (min-width: 600px) {
  #promo-section {
    background: url('/assets/images/ETlogos/logo.svg') no-repeat;
    background-size: 50% 250%;
    background-position: 100% 50%;
    padding: 3% 0;
  }
}
#page-login .login-container {
  max-width: 30em;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}
.login-with-button {
  position: relative;
  height: 40px;
  padding: 0.3em 0.6em;
  display: flex;
  flex-direction: row;
  align-items: center;
  background: transparent!default;
  border: none;
  border-radius: 4px;
  overflow: hidden;
  margin: 0.5em;
  min-width: 250px;
  text-decoration: none;
}
.login-with-button.facebook {
  background: #3b5998;
  color: white;
}
.login-with-button.google {
  background: white;
  color: rgba(0, 0, 0, 0.54);
}
.login-with-button.feide {
  background: #BE1527;
  color: white;
}
.login-with-button.disabled,
.login-with-button[disabled] {
  background: grey;
  color: rgba(0, 0, 0, 0.8);
  cursor: default;
}
.login-with-button > .logo-container {
  background: transparent;
  height: 100%;
  width: auto;
}
.login-with-button > .logo-container > svg,
.login-with-button > .logo-container img {
  height: 100%;
}
.login-with-button > .text-container {
  margin: auto 1em;
  text-decoration: none;
}
#page-profile {
  background-color: #eee;
}
#profile-main {
  max-width: 1000px;
}
#profile-picture {
  border-style: solid;
  border-width: 1px;
  border-color: #b71c1c;
  background-repeat: no-repeat;
  background-position: 50%;
  border-radius: 50%;
  width: 240px;
  height: 240px;
  margin: 10px;
  margin-bottom: 20px;
}
.profile-basics-table {
  line-height: 100%;
}
.profile-basics-table td {
  vertical-align: top;
}
.profile-basics-table i {
  opacity: 0.6;
}
.profile-basics-table span {
  opacity: 0.5;
}
h2:hover {
  cursor: pointer;
}
h2:hover span {
  display: inline;
}
.animate-repeat {
  line-height: 40px;
  list-style: none;
  box-sizing: border-box;
}
.dialogdemoBasicUsage #popupContainer {
  position: relative;
}
.dialogdemoBasicUsage .footer {
  width: 100%;
  text-align: center;
  margin-left: 20px;
}
.dialogdemoBasicUsage .footer,
.dialogdemoBasicUsage .footer > code {
  font-size: 0.8em;
  margin-top: 50px;
}
.ta-toolbar {
  background-color: rgba(200, 200, 200, 0.54);
  margin-bottom: 2px;
}
.ta-scroll-window.form-control {
  border: 1px solid rgba(0, 0, 0, 0.36);
}
.ta-button-group {
  display: flex;
  flex-direction: row;
  margin: 2px;
}
.ta-button-group > .ta-button {
  height: auto;
  width: auto;
  min-height: auto;
  min-width: auto;
  line-height: inherit;
  border-radius: 4px;
  padding: 6px 12px;
  margin: 0;
}
.ta-button-group > .ta-button:first-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.ta-button-group > .ta-button:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.ta-button-group > .ta-button:not(:first-child):not(:last-child) {
  border-radius: 0;
}
.ta-button-group > .ta-button:not([disabled]) {
  color: #333;
  background-color: #fff;
  border-color: #ccc;
}
.ta-button-group > .ta-button:not([disabled]):active,
.ta-button-group > .ta-button:not([disabled]).active {
  color: #333;
  background-color: #e0e0e0;
  border-color: #adadad;
}
.ta-button-group > .ta-button:not([disabled]):hover {
  color: #333;
  background-color: #ebebeb;
  border-color: #adadad;
}
.ta-button-group > .ta-button[disabled] {
  background-color: #dddddd;
  color: rgba(0, 0, 0, 0.54);
}
.ta-button {
  border-radius: 4px;
  border: 1px solid transparent;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
}
dl.dl-table dt,
dl.dl-table dd {
  display: inline-block;
  vertical-align: text-top;
  text-align: left;
}
dl.dl-table dt {
  width: 150px;
}
.bordered {
  border: 1px solid rgba(0, 0, 0, 0.54);
  border-radius: 4px;
  padding: 1em;
}
#profiles-container {
  max-width: 1024px;
}
#filter-button {
  min-width: 0px;
}
.animate-repeat {
  line-height: 40px;
  list-style: none;
  box-sizing: border-box;
}
/* endinject */
